import { validateForms } from '../functions/validate-forms.js';



const rules = [
	{
		ruleSelector: '.input-tel',
		tel: true,
		telError: '  ',
		showErrorMessage: false,
		rules: [
			{
				rule: 'required',
				errorMessage: '  !',
			},
			{
				rule: 'minLength',
				value: 10,
				errorMessage: '   10 ',
			},
			{
				rule: 'customRegexp',
				value: /^(\+?[\d\s\-\(\)]{7,})?$/,
				errorMessage: '  ',
			}
		]
	},
	{
		ruleSelector: '.input-name',
		rules: [
			{
				rule: 'minLength',
				value: 3,
				errorMessage: ' 3 ',
				showErrorMessage: false,
			},
			{
				rule: 'maxLength',
				value: 30,
				errorMessage: '   30 ',
			},
			{
				rule: 'required',
				errorMessage: ' " "   ',
			},
			{
				rule: 'customRegexp',
				value: /^[A-Za-z--\s]+$/,
				errorMessage: '      ',
			},
		]
	},
	{
		ruleSelector: '.input-email',
		rules: [
			{
				rule: 'required',
				errorMessage: ' "Email"   ',
			},
			{
				rule: 'email',
				errorMessage: '  email',
			},
			{
				rule: 'minLength',
				value: 7,
				errorMessage: 'Email    7 ',
			},
			{
				rule: 'maxLength',
				value: 30,
				errorMessage: 'Email    30 ',
			}
		]
	},
	{
		ruleSelector: '.input-theme',
		rules: [
			{
				rule: 'required',
				errorMessage: '    ',
			},
			{
				rule: 'minLength',
				value: 5,
				errorMessage: '    5 ',
			},
			{
				rule: 'maxLength',
				value: 30,
				errorMessage: '    40 ',
			},
		]
	},
];

const afterForm = () => {
	window.location.href = 'thanks.html';
};

const error = () => {
	// modalClickHandler('fail', 'active');
};


const forms = document.querySelectorAll('.main-form');

for (const form of forms) {
	validateForms(form, rules, [], afterForm);
}
